import React, { useState } from 'react';
import clsx from "clsx"
import { useFlexSearch } from "react-use-flexsearch"
import { useDispatch } from "react-redux"
import { guardarProductosBusquedaAction } from "../../../state/reducers/productoReducers"
import { fade, makeStyles } from '@material-ui/core/styles';
import { Link, navigate } from "gatsby"
import Menu from '@material-ui/core/Menu';
import Toolbar from '@material-ui/core/Toolbar';
import AppBar from '@material-ui/core/AppBar';
import IconButton from '@material-ui/core/IconButton';
import SwipeableDrawer from '@material-ui/core/SwipeableDrawer';
import InputBase from '@material-ui/core/InputBase';
import Badge from '@material-ui/core/Badge';
import MenuItem from '@material-ui/core/MenuItem';
import MenuIcon from "@material-ui/icons/MenuRounded";
import AddShoppingCartIcon from "@material-ui/icons/ShoppingCartRounded"
import SearchIcon from '@material-ui/icons/Search';
import AccountCircle from '@material-ui/icons/AccountCircleSharp';
import ListItemText from "@material-ui/core/ListItemText";
import Divider from "@material-ui/core/Divider";
import Grid from "@material-ui/core/Grid";
import List from "@material-ui/core/List";
import Paper from "@material-ui/core/Paper"
import Box from "@material-ui/core/Box";
import ListItem from "@material-ui/core/ListItem";
import AniLink from "gatsby-plugin-transition-link/AniLink";
import SentimentVeryDissatisfied from "@material-ui/icons/SentimentVeryDissatisfied";
import Typography from "@material-ui/core/Typography";
import DrawerHeder from "../DraweHeder"
import { useSelector } from "react-redux"
import { StaticImage } from "gatsby-plugin-image"
import RoomRoundedIcon from '@material-ui/icons/RoomRounded';
import Img from "gatsby-image"
const useStyles = makeStyles((theme) => ({
    root: {
        width: '1.3em',
        height: '1.3em',
        color: "#1f66ad"
    },
    iconsColor: {
        width: '1.3em',
        height: '1.3em',
        color: "#1f66ad"
    },
    logoHeader: {
       width:'140px',
       minHeight:'50px',
       minWidth:'26px',
       maxWidth:'190px',       
       height:'auto',       
    },
    grow: {
        flexGrow: 1,
    },
    menuButton: {
        marginRight: theme.spacing(0),
    },
    iconSearchHeader: {
        position: 'absolute',
    },
    title: {
        display: 'none',
        [theme.breakpoints.up('sm')]: {
            display: 'block',
        },
    },
    search: {
        position: 'relative',
        borderRadius: theme.shape.borderRadius,
        backgroundColor: fade(theme.palette.common.white, 0.15),
        '&:hover': {
            backgroundColor: fade(theme.palette.common.white, 0.25),
        },
        marginRight: theme.spacing(4),
        marginLeft: theme.spacing(3),
        width: '70%',
        [theme.breakpoints.up('sm')]: {
            marginLeft: theme.spacing(3),
            width: 'auto',
        },
        border: '2px solid #6f9dca',
    },
    searchIcon: {
        padding: theme.spacing(0, 2),
        height: '100%',
        position: 'absolute',
        pointerEvents: 'none',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
    },
    inputRoot: {
        color: 'inherit',
    },
    inputInput: {
        padding: theme.spacing(1, 1, 1, 0),
        // vertical padding + font size from searchIcon
        paddingLeft: `calc(1em + ${theme.spacing(4)}px)`,
        transition: theme.transitions.create('width'),
        width: '100%',
        [theme.breakpoints.up('md')]: {
            width: '20ch',
        },
    },
    sectionDesktop: {
        display: 'flex',
        [theme.breakpoints.up('md')]: {
            display: 'none',
        },
    },
    resultadobusqueda: {
        position: "fixed",
        width: "50%",
        paddingLeft: "15%",
        marginTop:'85px'
    },
    paperDescription: {
        height: 500,
        overflow: "auto",
    },
    estilosimagen: {
        maxWidth: "40%",
        marginLeft: 5,
        marginBottom: 0,
        marginTop: 0,
        padding: "0px",
        borderRadius: "100%"
    },
    colorApp: {
        background: "#fff",
        height: '105px'
    },    
    wrap: {
        width: '90%',
        position: 'absolute',
        top: '80%',
        left: '50%',
        transform: 'translate(-50%, -50%)'
    },
    searchInput: {
        width: '100%',
        position: 'relative',
        display: 'flex'
    },
    searchTerm: {
        width: '100%',
        border: '1px solid #6f9dca',
        borderRight: 'none',
        padding: '5px',
        height: '20px',
        borderRadius: '5px 0 0 5px',
        outline: 'none',
        color: 'gray'
    },
    searchButton: {
        width: '40px',
        height: '32px',
        border: '1px solid #97c93e',
        background: '#97c93e',
        textAlign: 'center',
        color: '#fff',
        borderRadius: '0 5px 5px 0',
        cursor: 'pointer',
        fontSize: '20px'
    },
    headerLogoContainer:{        
         display:'flex',        
         width:'30vw',
         minWidth:'100px',
         maxWidth:'90%',        
    }
}));

export default function HederMenu({ menuYSubmenu, index, store, segmento }) {
    const classes = useStyles();
    const dispatch = useDispatch()
    const { carrito } = useSelector(state => state.carrito)
    const [searchQuery, setSearchQuery] = useState("")
    const results = useFlexSearch(searchQuery, index, store)
    const hasNoResults = searchQuery.length > 0 && results.length === 0
    const MostrarBusquedaCatalogo = e => {
        if (e.keyCode === 13 || e.keyCode === 9) {
            e.preventDefault()
            dispatch(guardarProductosBusquedaAction(results))
            navigate(`/buscador/`)
        }
    }
    const BusquedaCatalogoClick = e => {
        e.preventDefault()
        navigate(`/buscador/`)
        dispatch(guardarProductosBusquedaAction(results))
    }

    const [mostrarDrawer, setDrawer] = React.useState(false);
    const toggleDrawerOpen = () => {
        setDrawer(true);
    };
    const toggleClose = () => {
        setDrawer(false);
    };
    const [anchorEl, setAnchorEl] = React.useState(null);
    const [mobileMoreAnchorEl, setMobileMoreAnchorEl] = React.useState(null);

    const isMenuOpen = Boolean(anchorEl);

    const handleProfileMenuOpen = (event) => {
        setAnchorEl(event.currentTarget);
    };

    const handleMobileMenuClose = () => {
        setMobileMoreAnchorEl(null);
    };

    const handleMenuClose = () => {
        setAnchorEl(null);
        handleMobileMenuClose();
    };



    const menuId = 'primary-search-account-menu';
    const renderMenu = (
        <Menu
            anchorEl={anchorEl}
            anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
            id={menuId}
            keepMounted
            transformOrigin={{ vertical: 'top', horizontal: 'right' }}
            open={isMenuOpen}
            onClose={handleMenuClose}
        >
            <MenuItem onClick={handleMenuClose}> <IconButton
                variant="extended"
                href="https://www.asidelimpio.com/login"
                target="_blank"

            >
                <h6>Login</h6>
            </IconButton></MenuItem>
            <MenuItem onClick={handleMenuClose}><IconButton
                variant="extended"
                href="https://www.disdelsagt.com/MyBusiness/Home/Login"
                target="_blank"

            >
                <h6>MyBusiness</h6>
            </IconButton></MenuItem>
        </Menu>
    );


    return (
        <div className={classes.grow}>
            <AppBar position="fixed" className={classes.colorApp}>
                <Toolbar>
                    <IconButton
                        onClick={toggleDrawerOpen}
                        edge="start"
                        className={classes.menuButton}
                        color="inherit"
                        aria-label="open drawer"
                        fontSize="large"
                    >
                        <MenuIcon
                            className={classes.root}
                        />
                    </IconButton>
                    <div style={{ display: "grid", width: '30%', height: '40', maxWidth: '94px' }}>
                        {results.length > 0 && (
                            <div className={clsx(classes.resultadobusqueda)}>
                                <div style={{ position: "absolute", paddingTop: "8%" }}>
                                    <Paper className={clsx(classes.paperDescription)}>
                                        <List>
                                            {results.map((item) => {
                                                return (
                                                    <ListItem button>
                                                        <Link
                                                            to={`/producto/${item.ItemCodeAux}/`}
                                                            style={{ textDecoration: "none" }}
                                                        >
                                                            <Grid
                                                                container
                                                                alignItems="center"
                                                                justify="center"
                                                            >

                                                                <img
                                                                    className={clsx(classes.estilosimagen)}
                                                                    src={
                                                                        "https://disdelsa.com/imagenes/productos/" +
                                                                        item.Imagen
                                                                    }
                                                                />
                                                                <ListItemText
                                                                    primary={
                                                                        <Box style={{ fontSize: 10, textDecoration: "none", color: "black" }}>
                                                                            {item.Descripcion}
                                                                        </Box>
                                                                    }
                                                                    className={clsx(classes.estilotexto)}
                                                                />
                                                            </Grid>
                                                            <Divider />
                                                        </Link>
                                                    </ListItem>
                                                );
                                            })}
                                        </List>
                                    </Paper>

                                </div>
                            </div>
                        )}
                        {hasNoResults && (
                            <div className={clsx(classes.resultadobusqueda)}>
                                <div style={{ position: "absolute", paddingTop: "2%" }}>
                                    <Paper
                                        className={clsx(
                                            classes.paperDescription

                                        )}
                                    >
                                        <div container>
                                            <div>
                                                <div className={clsx(classes.busqueda)}>
                                                    <SentimentVeryDissatisfied
                                                        fontSize="large"
                                                        style={{ fontSize: 40 }}
                                                    />
                                                    <Typography variant="h6" style={{ margin: "10px" }}>
                                                        Lo sentimos!
                                                    </Typography>
                                                </div>
                                                <Typography style={{ margin: "5px" }}>
                                                    no se encontraron coincidencias de busqueda.
                                                </Typography>
                                                <br />

                                                <strong>
                                                    Quizs puedan interesarte las siguientes secciones
                                                </strong>
                                                <Grid container>
                                                    <Grid item md={12} xs={12}>
                                                        <Grid container>
                                                            {segmento?segmento.map(
                                                                (item, index) => (
                                                                    <Grid
                                                                        item
                                                                        md={4}
                                                                        xs={3}
                                                                        key={index.toString()}
                                                                        style={{ margin: "0px" }}
                                                                    >
                                                                        <AniLink
                                                                            paintDrip
                                                                            hex="#9ccc65"
                                                                            to={`/${item.SegmentoAux}/`}
                                                                            entryOffset={80}
                                                                            duration={0.6}
                                                                        >
                                                                            <img
                                                                                src={`https://disdelsa.com/imagenes/${item.Imagen}`}
                                                                                alt={item.Imagen}
                                                                                style={{ padding: 15 }}
                                                                            />
                                                                        </AniLink>
                                                                        <Typography
                                                                            style={{
                                                                                fontFamily: "Lobster",
                                                                                textAlign: "center",
                                                                            }}
                                                                        >
                                                                            <Box fontWeight="fontWeightBold">
                                                                                {item.NombreSegmento}
                                                                            </Box>
                                                                        </Typography>
                                                                    </Grid>
                                                                )
                                                            ):null}
                                                        </Grid>
                                                    </Grid>
                                                </Grid>
                                            </div>
                                        </div>
                                    </Paper>
                                </div>
                            </div>
                        )}
                        <Link
                            to="/"
                        >
                            <div className={classes.headerLogoContainer}>
                            <img
                                src="https://disdelsa.com/imagenes/BannerImagen-img2059-4-2-2021-114805.png"                                                                 
                                alt="Disdel"                                
                                width={"100%"}                                                
                                formats={["AUTO", "WEBP", "AVIF"]}
                                style={{ color: "white" }}
                            />
                            </div>
                       
                        </Link>
                    </div>
                    <div className={classes.grow} />
                    <Link
                        to="/Contactanos"
                    >
                        <IconButton
                            edge="end"
                            aria-label="account of current user"
                            aria-controls={menuId}
                            aria-haspopup="true"
                            //onClick={handleProfileMenuOpen}
                            color="inherit"
                        >
                            <RoomRoundedIcon className={classes.iconsColor} />
                        </IconButton>
                    </Link>
                    <div className={classes.sectionDesktop}>
                        <Link
                            to="/Carrito"

                        >
                            <IconButton aria-label="show 17 new notifications" color="inherit">
                                <Badge badgeContent={carrito.length} color="secondary">
                                    <AddShoppingCartIcon className={classes.iconsColor} />
                                </Badge>
                            </IconButton>
                        </Link>
                        <IconButton
                            edge="end"
                            aria-label="account of current user"
                            aria-controls={menuId}
                            aria-haspopup="true"
                            onClick={handleProfileMenuOpen}
                            color="inherit"
                        >
                            <AccountCircle className={classes.iconsColor} />
                        </IconButton>
                    </div>
                </Toolbar>
                <div className={classes.wrap}>
                    <div className={classes.searchInput}>
                        <input
                            value={searchQuery}
                            onChange={e => setSearchQuery(e.target.value)}
                            placeholder="!Hola! Que estas buscando?"
                            onKeyDown={MostrarBusquedaCatalogo}
                            type="text" className={classes.searchTerm} />
                        <button
                            onClick={BusquedaCatalogoClick}
                            type="submit" className={classes.searchButton}>
                            <SearchIcon></SearchIcon>
                        </button>
                    </div>
                </div>
                <SwipeableDrawer
                    onClose={toggleClose}
                    onOpen={toggleClose}
                    open={mostrarDrawer}
                    anchor="left"
                >
                    <DrawerHeder menuYSubmenu={menuYSubmenu} />
                </SwipeableDrawer>
            </AppBar>

            {renderMenu}
        </div>
    );
}
