import React from "react"
import {
  IconButton,
  Badge,
  List,
  ListItem,
  Divider,
  ListItemAvatar,
  Avatar,
  ListItemText,
} from "@material-ui/core"
import { Link } from "gatsby"
import AddShoppingCartIcon from "@material-ui/icons/AddShoppingCart"
import Menu from "material-ui-popup-state/HoverMenu"
import { makeStyles } from "@material-ui/core/styles"
import {
  usePopupState,
  bindHover,
  bindMenu,
} from "material-ui-popup-state/hooks"
import { useSelector } from "react-redux"
const useStyles = makeStyles(({ palette, ...theme }) => ({
  menu: {
    maxHeight: 134 * 4,
    maxWidth: "40ch",
  },

  large: {
    width: theme.spacing(12),
    height: theme.spacing(12),
  },
}))

const Carrito = () => {
  const classes = useStyles()
  const { carrito } = useSelector(state => state.carrito)
  const popupState = usePopupState({ variant: "popover", popupId: "demoMenu" })
  return (
    <>
      <Link
        to="/Carrito"
        style={{
          cursor: "hand",
          background: "white",
          textDecoration: "none",
        }}
      >
        <IconButton
          className="mr-4 p-5 bg-light-primary rounded hover-bg-primary"
          {...bindHover(popupState)}
        >
          <Badge badgeContent={carrito.length} color="secondary">
            <AddShoppingCartIcon color="primary" fontSize="large" />
          </Badge>
        </IconButton>
      </Link>
      <Menu
        {...bindMenu(popupState)}
        className={classes.menu}
        getContentAnchorEl={null}
        anchorOrigin={{ vertical: "bottom", horizontal: "left" }}
        transformOrigin={{ vertical: "top", horizontal: "left" }}
      >
        {carrito.map((item, index) => (
          <List key={index}>
            <ListItem alignItems="flex-start">
              <ListItemAvatar>
                <Link to={`/producto/${item.ItemCodeAux}/`}>
                  <Avatar
                    alt="Remy Sharp"
                    src={`https://disdelsa.com/imagenes/productos/${item.Imagen}`}
                    className={classes.large}
                  />
                </Link>
              </ListItemAvatar>
              <ListItemText
                primary={`${item.IdProducto}`}
                secondary={<h6>{item.Descripcion}</h6>}
              />
            </ListItem>
            <Divider variant="inset" component="li" />
          </List>
        ))}
      </Menu>
    </>
  )
}

export default Carrito
